import { Notification } from 'element-ui';

/**
 * @description: 提示成功通知
 * @param {string} title 通知标题
 * @param {string} msg 通知内容
 * @return: void
 */
export function showSuccessNotify(title, msg) {
  Notification.success({
    title: title,
    message: msg,
    duration: 2000,
    position: 'bottom-right',
    showClose: true
  });
}

