 * @LastEditors: CH
 * @LastEditTime: 2021-03-03 19:49:15
 -->
<template>
  <div class="login_container" @keyup.enter="handleLoginEvent">
    <div class="login_box">
      <div class="margin-top-50">
        <img
          :src="require('../assets/img/Login/login_icon_Shattrath.png')"
          alt="沙塔信息技术有限公司"
        />
      </div>
      <div class="margin-bottom-30">
        <h1>短信验证码登录</h1>
      </div>
      <div class="margin-bottom-20">
        <el-input
          ref="phoneNumber"
          class="width-360 login-input"
          suffix-icon="login-account"
          placeholder="手机号"
          v-model.trim="phoneNumber"
          clearable
        ></el-input>
      </div>
      <div class="margin-bottom-15">
        <el-input
          ref="code"
          maxlength="6"
          class="width-360 login-input"
          suffix-icon="login-password"
          placeholder="验证码"
          v-model.trim="code"
          clearable
        >
          <el-button
            slot="append"
            type="button"
            @click="getCode"
            :disabled="!canClick"
            >{{ codeBtn }}</el-button
          >
        </el-input>
      </div>
      <div class="btnClass">
        <el-button class="width-360" type="primary" @click="handleLoginEvent"
          >立即登录</el-button
        >
        <el-button class="width-360" type="text" @click="toLogin"
          >账号密码登录</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { showErrorNotify } from '@/utils/notify';
import { STATUS_CODE, TITLE } from '@/utils/constantsFile';
// 导入所需接口
import { phoneLogin } from '@/api/public';
// 导入所需接口
import { userPhoneLoginRegisterSendCodeAction } from '@/api/management';
export default {
  name: 'Login',

  data() {
    return {
      // 能否点击
      codeBtn: '获取验证码',
      setIn: '',
      canClick: true,
      time: 59,
      // 手机号
      phoneNumber: '',

      // 密码
      code: ''

      // // 是否记住密码
      // isPasswordKept: true
    };
  },

  methods: {
    getCode() {
      var reg = /^1[0-9]{10}$/;
      if (
        this.phoneNumber === '' ||
        this.phoneNumber.length <= 10 ||
        !reg.test(this.phoneNumber)
      ) {
        this.$message({
          showClose: true,
          message: '请输入正确的手机号码!',
          type: 'warning'
        });
        this.$refs.phoneNumber.focus();
        return false;
      }
      this.toSend();
    },
    toSend() {
      var json = {
        phoneNumber: this.phoneNumber
      };
      userPhoneLoginRegisterSendCodeAction(json).then((res) => {
        // 请求数据正常
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          this.canClick = false;
          this.codeBtn = this.time-- + '秒';
          this.toTime();
          // 操作成功提示
          this.$message({
            showClose: true,
            message: res.message,
            type: 'success'
          });
        } else {
          // 操作异常提示
          this.$message({
            showClose: true,
            message: res.message,
            type: 'warning'
          });
        }
      });
    },
    toTime() {
      const _that = this;
      setTimeout(() => {
        if (_that.time === 0) {
          _that.codeBtn = '获取验证码';
          _that.canClick = true;
          _that.time = 59;
        } else {
          var num = _that.time--;
          if (num < 10) {
            _that.codeBtn = '0' + num + '秒';
          } else {
            _that.codeBtn = num + '秒';
          }
          _that.toTime();
        }
      }, 1000);
    },
    /**
     * @description: “立即登录”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleLoginEvent() {
      var reg = /^1[0-9]{10}$/;
      if (
        this.phoneNumber === '' ||
        this.phoneNumber.length <= 10 ||
        !reg.test(this.phoneNumber)
      ) {
        this.$message({
          showClose: true,
          message: '请输入正确的手机号码!',
          type: 'warning'
        });
        this.$refs.phoneNumber.focus();
        return false;
      }

      // 提示验证码不能为空
      if (!this.code) {
        this.$message({
          showClose: true,
          message: '验证码不能为空！',
          type: 'warning'
        });
        this.$refs.code.focus();
        return false;
      }
      this.login();
    },
    async login() {
      try {
        this.tableLoading = true;
        const res = await phoneLogin(
          {},
          {
            phoneNumber: this.phoneNumber,
            code: this.code
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          localStorage.setItem('token', JSON.stringify(res.rows.token));
          localStorage.setItem('realName', JSON.stringify(res.rows.realName));
          localStorage.setItem('userId', JSON.stringify(res.rows.userId));
          this.$router.push('/');
        } else {
          // 提示异常信息
          this.$message({
            showClose: true,
            message: res.message,
            type: 'warning'
          });
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    toLogin() {
      this.$router.push({
        path: '/login'
      });
    }
  },

  mounted() {
    // 用户名输入框自动获取焦点
    this.$refs.phoneNumber.focus();
  }
};
</script>

<style lang="less" scoped>
.login_container {
  background-color: #0871dc;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_box {
  width: 450px;
  height: 550px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.margin-top-50 {
  margin-top: 50px;
}

.width-360 {
  width: 360px;
}

.margin-bottom-40 {
  margin-bottom: 40px;
}

.margin-bottom-30 {
  margin-bottom: 30px;
}

.margin-bottom-20 {
  margin-bottom: 20px;
}

.margin-bottom-15 {
  margin-bottom: 15px;
}

.display-grid {
  display: grid;
  grid-template-columns: 180px 180px;
}

// “记住密码”checkbox靠左对齐
.justify-self-start {
  justify-self: start;
}

// “忘记密码”链接靠右对齐
.justify-self-end {
  justify-self: end;
}

// 调整输入框中的icon的位置
.login-input /deep/ .el-input__icon {
  margin-top: 4px;
}

.btnClass {
  text-align: center;
  margin-top: 62px;
}
</style>
